@import "./../../less/reset.less";
@arrow-bg:#4671c9;
@arrow-color: #fff;

/**
 * С��ͷ:˼·������border���������Σ�Ȼ����:before��:after�������������ȵ������ν��е���
 * @param @size        С��ͷ��С
 * @param @bgColor     ������ɫ
 * @param @arrowSize   С��ͷ�Ĵ�С
 * @param @arrowColor  ��ͷ��ɫ
 * @param @arrowHeight ��ͷ�ĸ߶�
 */
.tinyArrow (@size, @bgColor, @arrowSize, @arrowColor, @arrowHeight){
  position: relative;
  display: inline-block;
  width: @size;
  height: @size;
  background: @bgColor;
  border-radius: 50%;

  &:before,
  &:after {
    position: absolute;
    top: (@size - @arrowSize)/2;
    width: 0;
    height: 0;
    border: @arrowSize/2 solid transparent;
    content: ' ';
  }

  &:before {
    left: (@size - (@arrowSize/2))/2 + @arrowHeight ;
    border-left-color: @arrowColor;
  }
  
  &:after {
    left: (@size - (@arrowSize/2))/2;
    border-left-color: @bgColor;
  }
}

/**
 * С��ͷ:˼·����:afterαԪ����border��һ�������Σ�Ȼ�������������߱߿���Ȼ��rotate 45deg�Ϳ�����
 * @param @fontSize    �����Ĵ�С��width��height������������С��ȷ��
 * @param @bgColor     ������ɫ
 * @param @arrowColor  ��ͷ��ɫ
 */
.tinyArrow2 (@fontSize, @bgColor, @arrowColor){
  position: relative;
  display: inline-block;
  width: 1em;
  height: 1em;
  font-size: @fontSize;
  background: @bgColor;
  border-radius: 50%;

  &:after {
    position: absolute;
    width: 50%;
    height: 50%;
    top: (100%-50%)/2;
    left: (100%-50%)/2-10;
    border: 0.075em solid @arrowColor;
    border-bottom-color: transparent;
    border-left-color: transparent;
    transform: rotate3d(0,0,1,45deg);
    box-sizing: border-box;
    content: ' ';
  }
}
.tiny-arrow {
  .tinyArrow(50px, @arrow-bg, 35px, @arrow-color ,4px)
}
.tiny-arrow2 {
  .tinyArrow2(50px, @arrow-bg, @arrow-color);
}